.message {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-bottom: 20px;
}

.imageAndText {
    display: flex;
    align-items: flex-end;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    margin-right: 10px;
}

.text {
    position: relative;
    background-color: #007bff;
    color: white;
    padding: 10px;
    border-radius: 10px;
    max-width: 70%;
    word-wrap: break-word;
}

.text:after {
    content: '';
    position: absolute;
    right: -10px;
    bottom: 0;
    width: 0;
    height: 0;
    border-left: 10px solid #007bff;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
}

.name {
    font-weight: bold;
    margin-bottom: 5px;
}

.messageText {
    white-space: pre-wrap;
}

.time {
    font-size: 0.8em;
    margin-top: 5px;
}