.friendMessage {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
}

.friendImageAndText {
    display: flex;
    align-items: flex-end;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    margin-left: 10px;
}

.friendText {
    position: relative;
    background-color: #f1f1f1;
    color: black;
    padding: 10px;
    border-radius: 10px;
    max-width: 70%;
    word-wrap: break-word;
}

.friendText:after {
    content: '';
    position: absolute;
    left: -10px;
    bottom: 0;
    width: 0;
    height: 0;
    border-right: 10px solid #f1f1f1;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
}

.friendName {
    font-weight: bold;
    margin-bottom: 5px;
}

.friendMessageText {
    white-space: pre-wrap;
}

.friendTime {
    font-size: 0.8em;
    margin-top: 5px;
}